:root { 
    --black: #000000;
    --white: #ffffff;
   
    --font-size-l: 80px;
    --font-size-m: 30px;
    --font-size-s: 25px;
    --font-size-xl: 90px;
   
    --font-family-rubik: "Rubik", Helvetica;
  }
  .rubik-bold-white-80px {
    color: var(--white);
    font-family: var(--font-family-rubik);
    font-size: var(--font-size-l);
    font-style: normal;
    font-weight: 700;
  }
  